.profile {
    /* border: 1px solid red; */
    width: 300px;
    text-align: center;
    border-radius: 6px;
    color:bisque;
    background-color: rgb(172, 123, 218);
    box-shadow: 0px 5px 4px rgba(46, 47, 66, 0.08),
    0px 4px 4px rgba(46, 47, 66, 0.16),
    0px 4px 10px rgba(46, 47, 66, 0.08);
}
.description{
  display: flex;
  flex-direction: column;
  align-items: center;

}
.avatar{
    border-radius: 60%;
    padding-top: 30px;
    width: 200px;
    height: 200px;
    margin-bottom: 15px;
}
.name {
    font-size: 25px;
    margin-bottom: 12px;
    font-weight: 700;
}
.tag {
    margin-bottom: 12px;
    font-size: 17px;
    font-weight: 500;
}
.location {
    margin-bottom: 12px;
    font-size: 20px;
    font-weight: 600;
}
.stats {
    margin: 0;
    padding: 0;
    padding-top: 10px;
    padding-bottom: 8px;
    list-style: none;
    display: flex;
    justify-content: center;
    gap: 20px;
    color: rgb(78, 72, 72);
    background-color: rgb(235, 245, 147);
}
.label {
    display: flex;
    flex-direction: row;
    margin-bottom: 8px;
    font-weight: 700;


}
.quantity{
    align-items: center;
    text-align: center;
    font-weight: 500;
}
