 .friend_section{
    width: 300px;
}

.friend_list {
    display: flex;
    gap: 50px;
    flex-direction: column;
    list-style:circle;
}

.friend_item {
    height: 100px;
    list-style-position: outside;
    display: flex;
    gap: 20px;
    align-items: center;
    background-color:rgb(248, 250, 250);
    color:rgb(44, 94, 94);
    box-shadow: 0px 5px 4px rgba(46, 47, 66, 0.08),
    0px 4px 4px rgba(46, 47, 66, 0.16),
    0px 4px 10px rgba(46, 47, 66, 0.08),
    0px 4px 10px rgba(46, 47, 66, 0.08); 
}

 .avatar {
    width: 60px;
    height: 50px;
}

.name {
    font-size: 20px;
    font-weight: 600;
}

.true { 
    margin-left: 10px;
    width: 10px;
    height: 10px;
    background-color: green;
    border-radius: 50%;
    display: inline-block;
}

.false {
    margin-left: 10px;
    width: 10px;
    height: 10px;
    background-color: red;
    border-radius: 50%;
    display: inline-block;
}  