

.statistics {
    width: 500px;
    height: 200px;
    background-color: rgb(241, 174, 174);
    border-radius: 6px;
    box-shadow: 0px 5px 4px rgba(46, 47, 66, 0.08),
    0px 4px 4px rgba(46, 47, 66, 0.16),
    0px 4px 10px rgba(46, 47, 66, 0.08); 
}

.title {
    padding-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    font-size: 35px;
    font-weight: 700;
    color:azure;
}

.list {
    display: flex;
    gap: 15px;
}

.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 70px;
    height: 60px;
    background-color: aqua;
    box-shadow: 0px 5px 4px rgba(46, 47, 66, 0.08),
    0px 4px 4px rgba(46, 47, 66, 0.16),
    0px 4px 10px rgba(46, 47, 66, 0.08); 
}